import CONFIG from '../globals/config';
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';

class RestaurantItem extends HTMLElement {
  set restaurant(restaurant) {
    this.render(restaurant);
  }

  render(restaurant) {
    this.innerHTML = `
		<div class="restaurant-item">
			<div class="restaurant-item__header">	
				<img
						class="lazyload restaurant-item__header__poster"
						data-src="${CONFIG.BASE_IMAGE_URL}/${restaurant.pictureId}"
						alt"=${restaurant.name}"
				/>

				<div class="restaurant__item__info">
					<p>
					üìç<span>${restaurant.city}<span/>
					</p>
					<p>
					‚≠êÔ∏è<span>${restaurant.rating}<span/>
					</p>
				</div>
			</div>
			
			<div class="restaurant__item__body">
				<section class="restaurant__item__body__section">
					<h3>${restaurant.name}</h3>
					<p>${restaurant.description}...</p>
				</section>
				<a class="restaurant__item__body__link" href="/#/detail/${restaurant.id}" aria-label="View Detail ${restaurant.name}">
					<p>View Details...</p>
				</a>
			</div>
		
		</div>
		</div>
		`;
  }
}

customElements.define('restaurant-item', RestaurantItem);
