import CONFIG from '../globals/config';

class DetailItem extends HTMLElement {
  set restaurant(restaurant) {
    this.render(restaurant);
  }

  render(restaurant) {
    this.innerHTML = `
		<div class="header">
		<h2 class="title">${restaurant.name}</h2>
		<div class="likebutton" id="like-button"></div>
		</div>
			
		<img
				src="${CONFIG.BASE_IMAGE_URL}/${restaurant.pictureId}"
				alt="${restaurant.name}"
		/>
	
		<div class="categories">
			<h4>Categories:</h4>
				${restaurant.categories.map((category) => `<p>${category.name}</p>`).join('')}
		</div>

		<div class="location_rating">
			<h4>Location:</h4>
			<div>
				<p>üìç${restaurant.address}, ${restaurant.city}</p>
			</div>
			<h4>Rating:</h4>
			<div>
				<p>‚≠êÔ∏è ${restaurant.rating}</p>
			</div>
		</div>

		<hr />
		<h4>Description:</h4>
		<p class="description">
			${restaurant.description}
		</p>

			<div class="detail-foods-drinks">
				<div class="foods">
					<p>Foods</p>

					<ul class="food">
						${restaurant.menus.foods.map((food) => `<li>${food.name}</li>`).join('')}
					</ul>
				</div>

				<div class="foods">
					<p>Drinks</p>

					<ul class="drink">
						${restaurant.menus.drinks.map((drinks) => `<li>${drinks.name}</li>`).join('')}
					</ul>
				</div>
			</div>

			<hr />

			<div class="reviews">
			<h4>What Do You Think:</h4>
				<form id="reviewForm">
					<input type="hidden" name="id" value="${restaurant.id}" required />
					<input type="text" name="name" placeholder="Name" required />
					<textarea name="review" cols="10" rows="5" placeholder="Write review" required></textarea>
					<button>COMMENT</button>
				</form>
			<h4>Comments:</h4>
				<div id="review-container"></div>
			</div>
		`;
  }
}

customElements.define('detail-item', DetailItem);
